// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'
// CONFIG
import {db, au} from '../../Config/firebase'
// ICONS
import {FaStar} from 'react-icons/fa'

const OpinionsComponent = styled.div`
	
`
const Opinion = styled.div`
	margin: 10px;
`
const Star = styled(FaStar)`
	color: var(--color-decorative);
	font-size: 20px;
`
const Comment = styled.p`
	background-color: var(--color-dark);
	padding: 10px;
`

class Opinions extends Component {
	state = {
		opinions: []
	}
	componentDidMount() {
		db.collection('apps').doc('easy-calc').onSnapshot(snap => {
			console.log(snap.data().stats.opinions);
			const opinions = Object.values(snap.data().stats.opinions);
			console.log(opinions);
			this.setState({
				opinions
			})
		})
	}
	render() {
		return (
			<OpinionsComponent>
				{this.state.opinions.map(opinion => {
					return (
						<Opinion>
							{[...Array(opinion.stars)].map(() => <Star />)}
							<Comment>{opinion.comment}</Comment>
						</Opinion>
					)
				})}
			</OpinionsComponent>
		);
	}
}

export default Opinions