// BASIC
import React, { Component } from 'react'
import styled, { css, keyframes } from 'styled-components'
// IMAGES
import loader from '../../Images/arrows/loader.svg'
import vectorLogo from '../../Images/logo/logo.png'
// ANIMATIONS
import '../Styles/main-keyframes.css'

const hide = keyframes`
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
`
const PreloaderElement = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	top: 0;
	width: 100vw;
	height: 100vh;
	background-color: black;
	z-index: 100;
	${props =>
		props.hide &&
		css`
			animation: ${hide} 0.3s both;
		`
	}
	${props =>
		props.delete &&
		css`
			display: none;
		`
	}
`

const Animation = styled.div`
	display: none;
	position: relative;
	width: 500px;
	height: 500px;
	${props =>
		props.isLoaded &&
		css`
			display: flex;
		`
	}
`
const animation = keyframes`
	0% {
		opacity: 0;
		transform: scale(0.5) rotate(-5deg);
	}
	30% {
		opacity: 1;
		transform: scale(1) rotate(0);
	}
	50% {
		opacity: 1;
		transform: scale(1) rotate(0);
	}
	60% {
		opacity: 1;
		transform: scale(1.2) rotate(0);
	}
	100% {
		opacity: 1;
		transform: scale(0.5) rotate(0);
	}
`
const Logo = styled.img`
	width: 100%;
	position: absolute;
	left: 0;
	top: 0;
	transform: translateX(-100px);
	:first-child {
		animation: ${animation} 1.5s both;
	}
`

class Preloader extends Component {
	state = {
		isLoaded: false,
		isLoading: true,
		hide: false,
		delete: false
	}
	componentDidMount() {
		this.setState({isLoaded: true, isLoading: false})
		setTimeout(() => {this.setState({hide: true})}, 1000);
		setTimeout(() => {this.setState({delete: true})}, 1300);
	}
	componentWillUnmount() {
		this.setState({
			isLoaded: false,
			isLoading: true,
			hide: false,
			delete: false
		})
	}
	render() {
		return (
			<PreloaderElement hide={this.state.hide} delete={this.state.delete}>
				<Animation isLoaded={this.state.isLoaded}>
					<Logo src={vectorLogo} />
				</Animation>
			</PreloaderElement>
		);
	}
}

export default Preloader